"use client";
import React from "react";
import { useSnackbar } from "notistack";
import { Mail, Lock } from "lucide-react";
import { LoginProps } from "../../types/login";
import { useLogin } from "../../functions/login";
import Image from "next/image";

const LoginForm: React.FC<LoginProps> = ({
  onLogin,
  onForgotPassword,
  onSignUp,
  isLoading = false,
  error,
}) => {
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = React.useState(false);
  const { formData, handleInputChange, handleForgotPassword } = useLogin(
    undefined,
    onForgotPassword
  );

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      if (onLogin) {
        await onLogin(formData);
      }
    } catch (err: any) {
      // CrudFactory throws error object with message
    } finally {
      setLoading(false);
    }
  };

  return (
    // <div className="bg-linear-to-b rounded-2xl from-foreground via-foreground to-main-bg">
    <div
      className="w-full relative flex flex-col items-center justify-center px-12 z-10 "
      // style={{
      //   backgroundImage: "url('/images/login-illu-2.png')",
      //   backgroundRepeat: "no-repeat",
      //   backgroundSize: "cover",
      //   backgroundPosition: "center",
      // }}
    >
      {/* Optional: Overlay for readability */}
      {/* <div className="absolute inset-0 bg-main-bg/30 rounded-2xl pointer-events-none z-0" /> */}
      <div className="relative z-10 w-full">
        <h2 className="text-3xl font-bold text-white">Welcome Back!</h2>
        <p className="text-md font-thin text-white my-4">
          Login with your admin credential
        </p>

        <form className="w-full min-w-80 space-y-4" onSubmit={handleSubmit}>
          <div className="flex flex-col gap-4">
            <label htmlFor="email" className="text-white text-sm font-medium">
              Email
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Mail className="h-5 w-5 text-main-bg/70" />
              </div>
              <input
                id="email"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleInputChange}
                className="w-full pl-10 pr-4 py-3 rounded-lg border-2 border-main-bg/20 bg-white/10 text-secondary-bg placeholder-secondary-bg focus:outline-none focus:ring-2 focus:ring-blue-700 focus:border-transparent disabled:opacity-50"
                placeholder="Eg.: user@company.edu.in"
                disabled={loading}
                required
              />
            </div>

            <div className="space-y-2">
              <label
                htmlFor="password"
                className="text-white text-sm font-medium"
              >
                Password
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-main-bg/70" />
                </div>
                <input
                  id="password"
                  name="password"
                  type="password"
                  value={formData.password}
                  onChange={handleInputChange}
                  className="w-full pl-10 pr-4 py-3 rounded-lg border-2 border-main-bg/20 bg-white/10 text-secondary-bg placeholder-secondary-bg focus:outline-none focus:ring-2 focus:ring-blue-700 focus:border-transparent disabled:opacity-50"
                  placeholder="Use strong password"
                  disabled={isLoading}
                  required
                />
              </div>
            </div>
          </div>

          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <input
                id="remember"
                name="rememberMe"
                type="checkbox"
                checked={formData.rememberMe}
                onChange={handleInputChange}
                className="w-4 h-4 rounded border-white/20 bg-white/10 text-main-bg focus:ring-2 focus:ring-main-bg  disabled:opacity-50"
                disabled={loading}
              />
              <label htmlFor="remember" className="text-white text-sm">
                Remember me
              </label>
            </div>
            <button
              type="button"
              onClick={handleForgotPassword}
              className="text-red-800 my-4 hover:text-main-bg text-sm font-medium transition-colors disabled:opacity-50"
              disabled={loading}
            >
              Forgot password?
            </button>
          </div>

          <button
            type="submit"
            disabled={loading}
            className="w-full py-3 px-4 my-4 bg-black hover:bg-black/90 disabled:bg-gray-700 disabled:cursor-not-allowed text-white font-medium rounded-lg transition-colors focus:outline-none focus:ring-2 focus:ring-blue-400 focus:ring-offset-2 focus:ring-offset-transparent flex items-center justify-center"
          >
            {loading ? (
              <>
                <svg
                  className="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                >
                  <circle
                    className="opacity-25"
                    cx="12"
                    cy="12"
                    r="10"
                    stroke="currentColor"
                    strokeWidth="4"
                  ></circle>
                  <path
                    className="opacity-75"
                    fill="currentColor"
                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                  ></path>
                </svg>
                Signing In...
              </>
            ) : (
              "Sign In"
            )}
          </button>
        </form>
      </div>
      //{" "}
    </div>
  );
};

export default LoginForm;
